<template>
  <div class="hs-container">
    <c-header :title="$t('base.ForgetPassword')" isBackNav></c-header>
    <div class="title">
      <h1>{{$t('base.FillInYourBoundEmailAddress')}}</h1>
    </div>
    <div class="information">
      <hs-cell-1 :showLogo="false">
        <div class="custom-cell">
          <div class="logo" style="background-image: url('static/image/login/company-.png')"></div>
          <input maxlength='50' v-model="details.CmpId" :placeholder="$t('base.UnitNumberOrUnitName')"/>
        </div>
      </hs-cell-1>
      <hs-cell-1 :showLogo="false">
        <div class="custom-cell">
          <div class="logo" style="background-image: url('static/image/login/user-.png')"></div>
          <input maxlength='50' v-model="details.UserName" :placeholder="$t('base.Username')"/>
        </div>
      </hs-cell-1>
      <hs-cell-1 :showLogo="false">
        <div class="custom-cell">
          <div class="logo" style="background-image:url('static/image/login/forget-password/email.png')"></div>
          <input maxlength='50' v-model="details.Email" :placeholder="$t('base.Email')"/>
        </div>
      </hs-cell-1>
    </div>
    <hs-gradient-button style="margin-top: 50px" :title="$t('base.NextStep')" @click="_nextStep"></hs-gradient-button>
  </div>
</template>
<script type="text/ecmascript-6">
import CHeader from 'components/c-header/c-header'
import HsGradientButton from 'base/hs-button/hs-gradient-button'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
import {kpiUserModifyPasswordByEmail} from 'api'
export default {
  data() {
    return {
      details: {
        CmpId: '',
        UserName: '',
        Email: ''
      }
    }
  },
  methods: {
    _nextStep() {
      if (this.noValue(this.details.CmpId)) {
        this.$toast(this.$t('TM.PleaseEnter', 'function', this.$t('base.UnitNumberOrUnitName')))
        return
      } else if (this.noValue(this.details.UserName)) {
        this.$toast(this.$t('TM.PleaseEnter', 'function', this.$t('base.Username')))
        return
      } else if (this.noValue(this.details.Email)) {
        this.$toast(this.$t('TM.PleaseEnter', 'function', this.$t('base.Email')))
        return
      } else if (!this.isEmail(this.details.Email)) {
        this.$toast(this.$t('TM.XFormatIsIncorrect', 'function', this.$t('base.Email')))
        return
      }
      kpiUserModifyPasswordByEmail(this.details).then((res) => {
        console.log('忘记密码发送邮箱: ', res)
        if (res.IsSuccess) {
          this.$alert({
            content: this.$t('TM.SendMailSuccessfullyPleaseCheckTheMailboxToResetThePassword')
          })
        }
      })
    }
  },
  components: {
    CHeader,
    HsGradientButton,
    HsCell1
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-container
  width 100%
  height 100%
  >.title
    @extend .flex-row
    @extend .flex-start
    width 100%
    height 44px
    padding 0 10px
    >h1
      font-size 1rem
      font-weight bold
  >.information
    width calc(100% - 20px)
    margin 0 auto
    background-color $color_white
    border-radius 4px
    .custom-cell
      @extend .flex-row
      @extend .flex-start
      width 100%
      height 50px
      >.logo
        width 22px
        height 22px
        background-size 100% 100%
        margin-right 10px
      >input
        width calc(100% - 40px)
        height 40px
</style>



